* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

:root {
  --main: #0b0e11;
  --light-main: #151a21;
  --secondary: #fd4d4d;
  --font: #fff;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

body {
  background: #0b0e11;
  background: var(--main);
  color: #fff;
  color: var(--font);
  letter-spacing: 0.5px;
}

p,
a,
span,
ul,
ol,
li,
button,
div,
input {
  font-size: 14px;
}

a {
  color: #fff;
  color: var(--font);
  text-decoration: none;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: transparent;
}

::-webkit-scrollbar {
  width: 7px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #a7a4a4;
  border-radius: 5px;
}

